<template>
    <div class="page">
        <MyHeader>
            <div>孕娘支票</div>
            <span slot="right" class="icon-email" @click="doClick"></span>
        </MyHeader>
        <div class="content"></div>
    </div>
</template>

<script>
import "lib-flexible"
import MyHeader from "./header";
export default {
    name:"check",
    data:function(){
        return {
            title:"check"
        }
    },
    methods:{
        doClick:function(){
            console.log(1232)
        }
    },
    components:{MyHeader}
}
</script>

<style scoped>
.page{
    width:100%;
    height: auto;
    overflow: hidden;
}
.content{
    height: auto;
    overflow: hidden;
}
.icon-email{
    display: inline-block;
    float: right;
    width: 45px;
    height: 50px;
    background: url(./images/icon_email.png) no-repeat center right;
    background-size: 45px;
}
</style>



